import mixin from '../utils/mixins'
import {ol, proj4} from '../constants'
import BaseLayers from './baseLayer'
import _Controls from './Controls'
import _Interactions from './Interactions'
import _View from './View'
import Layer from '../layer/Layer'
import Feature from '../feature/feature'
import Style from '../style/Style'
import Overlay from '../overlay/Overlay'
import Observable from 'observable-emit'
class Map extends mixin(BaseLayers, _Controls, _Interactions, Style, Layer, _View, Feature, Overlay, Observable) {
  constructor () {
    super()
    this.addPointHandlerClick = null
    this.plotDraw = null // 标绘工具
    this.plotEdit = null
    this._lastDrawInteractionGeometry = null
    proj4.defs('EPSG:4490', '+proj=longlat +ellps=GRS80 +no_defs')
    ol.proj.setProj4(proj4)

    /**
     * 当前地图线要素
     * @type {Array}
     */
    this.currentMapLines = []
    /**
     * 当前地图点要素
     * @type {Array}
     */
    this.currentMapPoints = []
    /**
     * 当前地图面要素
     * @type {Array}
     */
    this.currentMapPolygon = []
    /**
     * 当前地图线图层
     * @type {Array}
     */
    this.lineLayers = new Set()
    /**
     * 当前地图点图层
     * @type {Array}
     */
    this.pointLayers = new Set()
    /**
     * 当前地图面图层
     * @type {Array}
     */
    this.polygonLayers = new Set()

    /**
     * 周边搜索要素
     * @type {null}
     */
    this.circleSerachFeat = null

    /**
     * 选择交互
     * @type {null}
     */
    this.selectInteraction = null

    /**
     * 移动交互
     * @type {null}
     */
    this.moveInteraction = null

    /**
     * 当前选择要素
     * @type {null}
     */
    this.currentSelectFeature = null

    /**
     * 上一次选择要素
     * @type {null}
     */
    this.lastSelectFeature = null
    /**
     * 当前地图气泡
     * @type {null}
     */
    this.popupOverlay = null

    /**
     * 当前视图
     * @type {null}
     */
    this.view = null

    /**
     * 定时器
     * @type {null}
     * @private
     */
    this.timer_ = null

    /**
     * map
     * @type {null}
     */
    this.map = null
    Observable.call(this)
  }

  /**
   * 初始化当前地图
   * @param mapDiv
   * @param params
   */
  initMap (mapDiv, params) {
    try {
      let options = params || {}
      let options_ = JSON.stringify(options)
      let logo = this._addCopyRight(options['logo'])
      this.view = this._addView(options['view'])
      let layers = this.addBaseLayers(options['baseLayers'], options['view'])
      let interactions = this._addInteractions(options['interactions'])
      let controls = this._addControls(options['controls'])
      /**
       * 当前地图对象
       * @type {ol.Map}
       */
      this.map = new ol.Map({
        target: mapDiv,
        loadTilesWhileAnimating: false,
        loadTilesWhileInteracting: false,
        logo: logo,
        layers: layers,
        view: this.view,
        interactions: interactions,
        controls: controls
      })

      /**
       * 保存当前参数
       */
      this.map.setProperties(options_, false)
      // 添加事件
      this._addEvent()
      this._addMapInteraction()

      /**
       * 加载成功事件
       */
      this.dispatch('loadMapSuccess', true)
    } catch (error) {
      this.dispatch('loadMapSuccess', error)
    }
  }

  /**
   * 添加事件
   * @private
   */
  _addEvent () {
    /**
     * 监听点击事件
     */
    this.map.on('click', event => {
      this.dispatch('click', event)
    })

    /**
     * 监听双击事件
     */
    this.map.on('dbclick', event => {
      this.dispatch('dbClick', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('singleclick', event => {
      this.dispatch('singleClick', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('movestart', event => {
      this.dispatch('moveStart', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('moveend', event => {
      this.dispatch('moveEnd', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('pointerdrag', event => {
      this.dispatch('pointerDrag', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('pointermove', event => {
      this.dispatch('pointerMove', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('postcompose', event => {
      this.dispatch('postCompose', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('postrender', event => {
      this.dispatch('postRender', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('precompose', event => {
      this.dispatch('preCompose', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('propertychange', event => {
      this.dispatch('propertyChange', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('change', event => {
      this.dispatch('change', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('change:layerGroup', event => {
      this.dispatch('changeLayerGroup', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('change:size', event => {
      this.dispatch('changeSize', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('change:target', event => {
      this.dispatch('changeTarget', event)
    })

    /**
     * 监听单击事件
     */
    this.map.on('change:view', event => {
      this.dispatch('changeView', event)
    })
  }

  /**
   * 添加地图交互
   * @private
   */
  _addMapInteraction () {
    // 添加选中交互
    this.selectInteraction = new ol.interaction.Select({
      condition: ol.events.condition.click,
      style: function (feature, resolution) {
        return [
          new ol.style.Style({
            stroke: new ol.style.Stroke({
              color: '#D97363',
              width: 10
            })
          })
        ]
      },
      layers: function (layer) {
        return (layer.get('selectable') === true)
      }
    })
    // 添加鼠标移动交互
    this.moveInteraction = new ol.interaction.Select({
      condition: ol.events.condition.pointerMove,
      style: function (feature, resolution) {
        return [
          new ol.style.Style({
            stroke: new ol.style.Stroke({
              color: '#D97363',
              width: 10
            })
          })
        ]
      },
      layers: function (layer) {
        return (layer.get('selectable') === true)
      },
      filter: function (feat, layer) {
        if (feat.get('features')) {
          return feat.get('features').length <= 1
        }
        return true
      }
    })
    this.moveInteraction.on('select', event => {
      let [selected, feature] = [event.selected, null]
      if (selected.length === 0) {
        let deselected = event.deselected
        if (deselected.length > 0) {
          feature = deselected[0]
          this.unHighLightFeature('', feature, '')
          if (feature && feature instanceof ol.Feature) {
            this.dispatch('feature:onmouseout', {
              type: 'feature:onmouseout',
              originEvent: event,
              value: feature
            })
          }
        }
      } else {
        feature = selected[0]
        // 如果两个要素距离太近，会连续选中，而无法得到上一个选中的要素，所以在此保留起来
        if (this.lastSelectFeature && this.lastSelectFeature instanceof ol.Feature) {
          this.unHighLightFeature('', this.lastSelectFeature, '')
          this.lastSelectFeature = null
        }
        this.lastSelectFeature = feature
        this.highLightFeature('', feature, '')
        if (feature && feature instanceof ol.Feature) {
          this.dispatch('feature:onmouseover', {
            type: 'feature:onmouseover',
            originEvent: event,
            value: feature
          })
        }
      }
    })
    this.selectInteraction.on('select', event => {
      let [selected, feature] = [event.selected, null]
      if (selected.length === 0) {
        let deselected = event.deselected
        if (deselected.length > 0) {
          feature = deselected[0]
          this.unHighLightFeature('', feature, '')
          if (feature && feature instanceof ol.Feature) {
            this.dispatch('feature:ondisselect', {
              type: 'feature:ondisselect',
              originEvent: event,
              value: feature
            })
          }
        }
      } else {
        feature = selected[0]
        // 如果两个要素距离太近，会连续选中，而无法得到上一个选中的要素，所以在此保留起来
        if (this.lastSelectFeature && this.lastSelectFeature instanceof ol.Feature) {
          this.unHighLightFeature('', this.lastSelectFeature, '')
          this.lastSelectFeature = null
        }
        this.lastSelectFeature = feature
        this.highLightFeature('', feature, '')
        if (feature && feature instanceof ol.Feature) {
          this.dispatch('feature:onselect', {
            type: 'feature:onselect',
            originEvent: event,
            value: feature
          })
        }
      }
    })
    this.map.addInteraction(this.moveInteraction)
    this.map.addInteraction(this.selectInteraction)
  }

  _addFeatureSelectEvent (event) {
    let feature = this.map.forEachFeatureAtPixel(event.pixel, function (feature) {
      return feature
    })
    if (feature && feature instanceof ol.Feature) {
      this.dispatch('feature:onmouseover', {
        type: 'feature:onmouseover',
        originEvent: event,
        value: feature
      })
    }
  }

  /**
   * 添加版权信息
   * @returns {boolean}
   * @private
   */
  _addCopyRight (params) {
    let logo = false
    if (params && typeof params !== 'object') {
      logo = {
        href: 'https://aurorafe.github.io',
        src: 'data:image/png;base64,'
      }
    } else if (typeof params === 'object') {
      logo = params
    } else {
      logo = false
    }
    return logo
  }

  /**
   * 获取当前地图
   * @returns {ol.Map}
   */
  getMap () {
    return this.map
  }

  /**
   * 设置地图实例
   * @param map
   */
  setMap (map) {
    if (map && map instanceof ol.Map) {
      this.map = map
    }
  }

  /**
   * 更新地图
   */
  updateSize () {
    this.map.updateSize()
  }
}
export default Map



